import { z } from "zod"
import { CqsScopeSchema } from "@rp/common/api-types"

const FormSchema = z.object({
  title: z.string().min(1, "Title is required"),
  prompt: z.string().min(1, "Prompt is required"),
  scope: z.union([CqsScopeSchema, z.literal("auto")]).default("auto"),
  timeRange: z.string().default("last 30 days"),
})

const TIME_RANGES = [
  "last 12 hours",
  "last 24 hours",
  "last 7 days",
  "last 30 days",
  "last 90 days",
  "last 120 days",
  "last 365 days",
]

type FormValues = z.infer<typeof FormSchema>

export type { FormValues }
export { FormSchema, TIME_RANGES }
